.container {
  background-color: #1A1A1A;
  position: fixed;
  bottom: -50px;
  right: 40px;
  z-index: 200;
  padding: 10px 12px;
  border-radius: 10px;
  border: 2px inset #0e0e0e;
  transition: background-color 500ms, bottom 1500ms;
}
.container .arrowTop {
  color: white;
}
.container:hover {
  cursor: pointer;
  background-color: rgb(49, 49, 49);
}

.showScroll {
  bottom: 50px;
}

