.container {
  background-color: white;
  box-shadow: 0px 1px 5px 1px black;
  min-width: 400px;
  display: flex;
  border-radius: 25px;
  justify-content: space-between;
  @media(max-width: 405px) {
    min-width: 100%;
  }

  &.selectedEmployee {
    background-color: #a7d79bab;
    border: 2px inset #8edd8ed6;
    box-shadow: 0px 0px 2px 1px #046304;
  }

  .blockForImage {
    background-color: black;
    width: 45%;
    position: relative;
    display: flex;
    justify-content: center;
    border-radius: 25px;
    overflow: hidden;

    img {
      height: 140px;
      z-index: 25;
      @media (max-width: 1000px) {
        height: 120px;
      }
    }

    .background {
      width: 100%;
      height: 140px;
      z-index: 20;
      position: absolute;
      top: 0;
      filter: blur(30px);
      @media (max-width: 1000px) {
        height: 120px;
      }
    }
  }

  .rightPart {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 55%;
    row-gap: 15px;
    @media (max-width: 1000px) {
      row-gap: 10px;
    }

    h5 {
      font-weight: 400;
      font-size: 19px;
      letter-spacing: 1px;
      color: #333333;
      text-shadow: 0px 1px 1px #757575;
    }

    button {
      padding: 5px 25px;
      border-radius: 10px;
      background-color: #014D55;
      color: white;
      font-size: 14px;
      border: 1px solid #003338;
      letter-spacing: 1px;
      text-shadow: 0px 1px 1px gray;
      transition: all 400ms ease;
      @media (max-width: 1000px) {
        padding: 5px 20px;
        font-size: 12px;
        letter-spacing: 0px;
      }

      &:hover {
        cursor: pointer;
        background-color: #013c42;
      }
    }
  }
}
