.blockHour {
  padding: 7px 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  //width: 9%;
  width: min-content;
  min-width: 120px;
  background-color: white;
  box-sizing: border-box;
  box-shadow: 0px 0px 20px 1px inset rgba(200, 201, 201, 0.82);
  border-radius: 10px;
  @media(max-width: 500px) {
    text-align: center;

    min-width: 80px;
  }

  &:hover {
    cursor: pointer;
  }

  .hour {
    font-size: 18px;

    & > span {
      @media(max-width: 500px) {
        display: none;
      }
    }
  }
}
